import React, { useState, useEffect, useCallback } from 'react'
import { Image, Table, Card, Button, Header } from 'semantic-ui-react'
import { connect } from "react-redux"
import erc20abi from '../../../ABI/ERC20.abi.json';
import LGOracleABI from '../../../ABI/LGOracle.json';
import BigNumber from 'bignumber.js';
import { getChainInfo } from '../../../components/global/functions';
import tokenList from '../../../tokenList/hangQinTokenList/index.json'
import { processPrice, createMulticall, AllToken } from './components/computer'


function Index(props: any) {

  const [, setLocalPrice] = useState<{
    prices: { address: string; price: number }[];
    time: number;
  }>({ prices: [], time: 0 });

  const [List, setList] = useState<{
    address: string;
    name: string;
    URI: string;
    balance: number;
    originPrice: number; //未作处理的价格
    price: number | string;
    upDown: number
  }[]>([]);

  const [titleHour, setTitleHour] = useState<number>(0)

  useEffect(() => {
    const newItems = tokenList.tokens.map(token => ({
      address: token.address,
      name: token.symbol,
      URI: token.logoURI,
      balance: 0,
      originPrice: 0,
      price: 0,
      upDown: 0,
    }));
    setList(newItems);

    const titleHour = () => {
      const parsedData = getLocalPrice()
      if (parsedData && parsedData.time !== undefined) {
        const time = getTime()
        const titleHour = Number(((time - parsedData.time) / 3600).toFixed(0))
        setTitleHour(titleHour)
      }
    }
    titleHour()
  }, []);

  const updateBalance = useCallback(async () => {
    const result = await getChainInfo(props.ChainID)
    const alltoken = await AllToken(result.ChainAdr.LGOracle)
    const multicall = createMulticall(result.ChainAdr.multicall)
    //创建调用列表
    const Calls = []
    //遍历token，创建调用列表
    for (let i = 0; i < alltoken.length; i++) {
      const call = {
        reference: alltoken[i],
        contractAddress: alltoken[i],
        abi: erc20abi.abi,
        calls: [{
          reference: alltoken[i],
          methodName: 'balanceOf',
          methodParameters: [props.UserAdr]
        }]
      }
      Calls.push(call)
    }
    const balance = await multicall.call(Calls);
    const balances = balance.results
    const balanceList: { key: string, balance: any }[] = []
    Object.entries(balances).forEach(([key, value]) => {
      balanceList.push({
        key: key,
        balance: value.callsReturnContext[0].returnValues[0]
      });
    });
    setList(prevList => {
      const updatedList = prevList.map(item => {
        const index = balanceList.findIndex((balanceListItem: { key: string; }) => balanceListItem.key === item.address);
        if (index !== -1) {
          let __b = (balanceList[index].balance);
          let _b = new BigNumber(__b.hex);
          let balance: number = Number(_b.dividedBy(10 ** 18).toNumber().toFixed(2));
          return {
            ...item,
            balance: balance,
          };
        }
        return item;
      });
      return updatedList;
    });
  }, [props.ChainID, props.UserAdr]);

  const getTime = (): number => {
    const currentDate = new Date();
    const milliseconds = currentDate.getTime();
    const time = Math.floor(milliseconds / 1000);
    return time;
  }


  const updatePrice = useCallback(async () => {
    const time = getTime();

    const result = await getChainInfo(props.ChainID)
    const alltoken = await AllToken(result.ChainAdr.LGOracle)
    const multicall = createMulticall(result.ChainAdr.multicall)
    //创建调用列表
    const Calls = []
    //遍历token，创建调用列表
    for (let i = 0; i < alltoken.length; i++) {
      const call = {
        reference: alltoken[i],
        methodName: 'PriceRMB',
        methodParameters: [alltoken[i]]
      }
      Calls.push(call)
    }
    //创建调用列表
    const priceCall = [{
      reference: 'PriceRMB',
      contractAddress: result.ChainAdr.LGOracle,
      abi: LGOracleABI,
      calls: Calls
    }]
    //调用multicall
    const _prices = await multicall.call(priceCall);
    const prices = _prices.results.PriceRMB.callsReturnContext  //链上价格
    //更新列表
    setList(prevList => {
      const updatedList = prevList.map(item => {
        const index = prices.findIndex((priceItem: { reference: string; }) => priceItem.reference === item.address);
        if (index !== -1) {
          let __p = prices[index].returnValues[0].hex;
          let _p = new BigNumber(__p);
          let p: number = _p.dividedBy(10 ** 8).toNumber();
          let Price: number | string = processPrice(p);
          let updow: number = 0;

          let ParsedData = getLocalPrice()
          if (ParsedData) {
            const prices = ParsedData.prices
            const index = prices.findIndex((priceItem: { address: string; }) => priceItem.address === item.address)
            if (index !== -1) {
              updow = Number(((p / prices[index].price - 1) * 100).toFixed(2));
            }
          }

          // 更新 localPrice 状态
          const SetLocalPrice = () => {
            setLocalPrice(prevLocalPrice => {
              const updatedLocalPrice = [...prevLocalPrice.prices];
              const localIndex = updatedLocalPrice.findIndex(localItem => localItem.address === item.address);
              if (localIndex !== -1) {
                updatedLocalPrice[localIndex] = {
                  address: item.address,
                  price: p,
                };
              } else {
                updatedLocalPrice.push({
                  address: item.address,
                  price: p,
                });
              }
              // 存入本地存储
              localStorage.setItem('localPrice', JSON.stringify({
                prices: updatedLocalPrice,
                time: time
              }));

              return {
                prices: updatedLocalPrice,
                time: time
              }

            });
          }

          //更新本地价格缓存
          let parsedData = getLocalPrice()
          if (parsedData && parsedData.time !== undefined && parsedData.time+86400 <= time) {
            SetLocalPrice()
          }
          if (!parsedData) {
            SetLocalPrice()
          }

          return {
            ...item,
            originPrice: p,
            price: Price,
            upDown: updow
          };

        }

        return item;
      });
      return updatedList;
    });

  }, [props.ChainID, setList]);

  const getLocalPrice = () => {
    const storedData = localStorage.getItem('localPrice');
    let parsedData: { prices: { address: string; price: number }[]; time: number } | null = null;
    if (storedData) {
      parsedData = JSON.parse(storedData) as { prices: { address: string; price: number }[]; time: number };
    }
    return parsedData;
  }

  // useEffect(() => {
  //   const intervalId = setInterval(() => {
  //     updatePrice()
  //     console.log(1)
  //   },10000);
  //   return () => clearInterval(intervalId);
  // }, [updatePrice]);

  useEffect(() => {
      updatePrice()
      updateBalance()
  }, [updatePrice, updateBalance]);

  const zhangStyle = {
    color: 'white',
    fontWeight: 900,
    backgroundColor: 'green'
  }

  const dieStyle = {
    color: 'white',
    fontWeight: 900,
    backgroundColor: 'red'
  }

  const renderTableRow = (item: any) => (
    <Table.Row key={item.address}>
      <Table.Cell>
        <Header as='h4' image>
          <Image src={`${process.env.PUBLIC_URL}/logo/${item.URI}`} rounded style={{ width: '22px', height: '22px' }} />
          <Header.Content style={{ color: 'white' }}>
            {item.name}
          </Header.Content>
        </Header>
      </Table.Cell>
      <Table.Cell>{item.balance}</Table.Cell>
      <Table.Cell textAlign="right">{item.price}</Table.Cell>
      <Table.Cell style={{ paddingTop: '2px', paddingBottom: '2px' }}>
        <Button icon floated='right' style={item.upDown >= 0 ? zhangStyle : dieStyle}>{item.upDown}%</Button>
      </Table.Cell>
    </Table.Row>
  );

  return (
    <div>
      <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '0px auto auto auto' }}>
        <Table celled fixed singleLine unstackable color='violet' inverted>
          <Table.Header  >
            <Table.Row >
              <Table.HeaderCell textAlign="center" style={{ width: '85px' }}>通证</Table.HeaderCell>
              <Table.HeaderCell >余额</Table.HeaderCell>
              <Table.HeaderCell textAlign="right" style={{ width: '80px' }}>价格￥</Table.HeaderCell>
              <Table.HeaderCell textAlign="right" style={{ width: '90px' }}>{titleHour}h↑↓</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body >
            {List.map((item) => renderTableRow(item))}

          </Table.Body>
        </Table>
      </Card>
    </div>
  )

}

const mapStateToProps = (state: any) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    sendStore(tp: any, val: any) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Index)

