import { Multicall } from 'ethereum-multicall';
import web3 from "../../../../ABI/web3";
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import LGOracleABI from '../../../../ABI/LGOracle.json';

export const processPrice = (p: number): number | string => {
  if (p < 0.01) {
    let pString: string = p.toString();
    let zeroCount = pString.split('.')[1].match(/^0*/)?.[0].length || 0;
    let decimalPart = (pString.split('.')[1].replace(/^0+/, '')).substring(0, 2);
    return `0.(${zeroCount})${decimalPart}`;
  } else if (p > 1000) {
    return p.toFixed(0);
  } else {
    return p.toFixed(3);
  }
};

export const createMulticall = (adr: string) => {
  const multicall = new Multicall({
    web3Instance: web3, tryAggregate: true,
    multicallCustomContractAddress: adr
  });
  return multicall;
}

export const AllToken = async(LGOracleAdr: string): Promise<string[]> =>{
  //获取LGOracle合约实例
  const LGOracleContract = new web3.eth.Contract(LGOracleABI as AbiItem[], LGOracleAdr)
  //获取所有token
  const alltoken = await LGOracleContract.methods.getAllTokens().call()
  return alltoken
}